/* card sizing and background image placement */
div.card-back {
  width: 100%;
  aspect-ratio: 5 / 7;
  max-width: 500px;
  max-height: 700px;
  border-radius: calc(25px * var(--scale));
  background-size: 110%;
  background-repeat: no-repeat;
  background-position: center center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

div.card-back.gbcp {
  background-size: 100%
}

/* scalable overlay */
.card-back .overlay {
  position: absolute;
  width: 500px;
  height: 700px;
  transform: scale(var(--scale));
}

.card-back .container {
  position: absolute;
  top: 20px;
  width: 100%;
  height: 565px;
  box-sizing: border-box;
  padding-left: 24px;
  padding-top: 3px;
  padding-right: 24px;
  /* background-color: aqua; */
  display: flex;
  flex-direction: column;
}

.card-back .name-plate {
  display: flex;
  flex-direction: row;
  width: 100%;
}

/* icon placement and styling */
.card-back .name-plate .guild-icon {
  width: 45px;
  height: 45px;
  /* margin: 8px 3px 0px -3px; */
  display: flex;
  overflow: visible;
  align-items: center;
  justify-content: center;
}
.card-back .name-plate .guild-icon svg {
  flex-shrink: 0;
  filter: drop-shadow(0px 0px 2px var(--mom-color, none));
}

.card-back .name-plate .name {
  display: flex;
  overflow: hidden;
  font-family: "IM Fell Great Primer SC", serif;
  font-weight: 400;
  font-size: 40px;
  /* letter-spacing: -0.05em; */
  letter-spacing: -0.01em;
  margin-top: -8px;
  border-bottom: 1px solid black;
  border-image: linear-gradient(to left, transparent, black 150px) 1;
  border-image-slice: 1;
  width: 100%;
}

/* drop cap (only does first letter) */
/* TODO, fix multi-word name drop-cap style */
.card-back .name div:first-letter {
  float: left;
  padding-top: 0.075em;
}
/* .header:first-letter {
  float: left;
  padding-top: 0.075em;
} */

/* applies to all headers */
/* character traits, heroic plays, legendary plays */
.card-back .header {
  text-align: left;
  font-family: "IM Fell Great Primer SC", serif;
  font-size: 34px;
  letter-spacing: -0.02em;
  border-bottom: 1px solid black;
  border-image: linear-gradient(to left, transparent, black 150px) 1;
  /* border-image-slice: 1; */
  line-height: 34px;
}

.card-back .character-trait {
  text-align: left;
}

.card-back .character-trait .trait {
  font-family: "Crimson Text", serif;
  font-weight: 800;
  font-style: italic;
  font-size: 21px;
}

.card-back .character-trait .trait.active > div::before {
  content: "◉ ";
  font-style: normal;
  font-weight: 400;
  font-family: "Source Sans Pro", sans-serif;
}

.card-back .character-trait .trait > div > span:nth-child(2) {
  font-family: "Crimson Text", serif;
  font-weight: 600;
  font-style: normal;
  font-size: 21px;
  letter-spacing: -1px;
}

.card-back .character-trait .text {
  font-family: "Crimson Text", serif;
  font-size: 21px;
  line-height: 22px;
  /* letter-spacing: -1px; */
  text-align: left;
  white-space: pre-wrap;
}

.card-back :is(.legendary, .heroic) {
  font-family: "Crimson Text", serif;
  font-size: 21px;
  line-height: 22px;
  text-align: left;
  white-space: pre-wrap;
}
.card-back :is(.heroic, .legendary) > div:first-child > span:first-child {
  font-family: "Crimson Text", serif;
  font-weight: 800;
  font-style: italic;
  font-size: 21px;
}
.card-back :is(.legendary, .heroic) > div:first-child > span:nth-child(2) {
  font-family: "Crimson Text", serif;
  font-weight: 600;
  font-style: normal;
  font-size: 21px;
  letter-spacing: -1px;
}

.card-back .footer {
  position: absolute;
  top: 595px;
  width: 100%;
  height: 60px;
  box-sizing: border-box;
  padding-left: 24px;
  padding-right: 24px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  /* background-color: grey; */
}

.card-back .footer .tags {
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-size: 21px;
  line-height: 20px;
  text-align: left;
  white-space: pre-wrap;
}

.card-back .footer .right {
  display: flex;
  flex-direction: column;
}

.card-back .footer .icons {
  display: flex;
  flex-direction: row-reverse;
  align-items: right;
  justify-content: right;
}
.card-back .footer .icons .wrapper {
  width: 44px;
  height: 44px;
  display: flex;
  align-content: center;
  justify-content: center;
  overflow: visible;
}
.card-back .footer .icons svg {
  height: 44px;
  flex-shrink: 0;
}

.card-back .footer .base-size {
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-size: 21px;
  line-height: 20px;
  text-align: right;
}
