/* card sizing and background image placement */
div.card-front {
  width: 100%;
  /* width: 2.5in; */
  /* height: 3.5in; */
  aspect-ratio: 5 / 7;
  max-width: 500px;
  max-height: 700px;
  border-radius: calc(25px * var(--scale));
  background-size: 110%;
  background-repeat: no-repeat;
  background-position: center center;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

div.card-front.gbcp {
  background-size: 100%
}

/* scalable overlay */
.card-front > .overlay {
  position: absolute;
  width: 500px;
  height: 700px;
  transform: scale(var(--scale));
}

/* top box positioning and layout */
.card-front .name-plate {
  position: absolute;
  top: 15px;
  left: 0px;
  width: 310px;
  height: 67px;
  display: flex;
  flex-direction: row;
  padding-left: 20px;
  padding-top: 7px;
  display: flex;
  flex-direction: row;
}

/* icon placement and styling */
.card-front .name-plate .guild-icon {
  width: 65px;
  height: 65px;
  overflow: visible;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-front .name-plate .guild-icon svg {
  flex-shrink: 0;
  filter: drop-shadow(0px 0px 2px var(--mom-color, none));
}

/* sub-box placement */
.card-front .name-plate > .name-plate-right {
  overflow: visible;
  display: flex;
  width: 100%;
  flex-direction: column;
}

.card-front .name-plate-right > .name {
  display: flex;
  overflow: hidden;
  font-family: "IM Fell Great Primer SC", serif;
  font-weight: 400;
  font-size: 40px;
  letter-spacing: -0.05em;
  /* letter-spacing: -0.01em; */
  margin-top: -8px;
  /* check vHarriet before touching this */
  line-height: 125%;
}

/* drop cap (only does first letter) */
/* TODO, fix multi-word name drop-cap style */
.card-front .name-plate .name > div:first-letter {
  float: left;
  padding-top: 0.075em;
}

.card-front .name-plate-right > .reach {
  position: absolute;
  bottom: 0px;
  width: 230px;
  font-family: "Crimson Text", serif;
  font-size: 21px;
  border-top: 1px solid black;
  border-image: linear-gradient(to left, transparent, black 150px) 1;
  border-image-slice: 1;
  text-align: left;
}

/* stat box */
.card-front .statbox {
  /* positioning */
  position: absolute;
  top: 105px;
  left: 24px;
  width: 293px;
  /* grid layout */
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  /* text styling */
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-size: 20px;
  letter-spacing: -1px;
  text-align: center;
}

/* interior border */
.card-front .statbox > :nth-child(-n + 6) {
  border-bottom: 1px solid black;
}
.card-front .statbox > :not(:nth-child(6n + 1)) {
  border-left: 1px solid black;
}

/* playbook */
.card-front .playbook {
  /* positioning */
  position: absolute;
  top: 167px;
  left: 24px;
  /* grid layout */
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 7px;
}

/* playbook result display */
.card-front .playbook-result {
  width: 45px;
  height: 45px;
  border-radius: 45px;
  box-sizing: border-box;
  border: 2px solid black;
  background-color: white;
  /* text-align: center; */
  /* line-height: 43px; */
  display: flex;
  align-items: center;
  justify-content: center;
  fill: black;
}

/* momentus result display overrides */
.card-front .playbook-result.momentus {
  border-color: var(--mom-border-color, black);
  background-color: var(--mom-color, var(--team-color));
  fill: white;
}

/* blank spacer display overrides */
.card-front .playbook-result.spacer {
  border: none;
  background-color: transparent;
}

/* character play box */
.card-front .character-plays {
  /* placement */
  position: absolute;
  top: 272px;
  width: 390px;
  padding: 5px 0 0 24px;
  /* layout */
  display: grid;
  grid-template-columns: 232px repeat(4, 1fr);
  /* align-items: flex-end; */
  text-align: center;
  /* default text styling, used from the CP attributes */
  font-family: "Crimson Text", serif;
  font-weight: 400;
  font-size: 20px;
}
/* first row borders */
.card-front .character-plays > :nth-child(-n + 5) {
  padding: 2px;
  border-bottom: 1px solid black;
  border-left: 1px solid black;
  letter-spacing: -1px;
}
/* "Character Plays" header */
.card-front .character-plays > :first-child {
  font-family: "IM Fell Great Primer SC", serif;
  font-weight: 400;
  font-size: 28px;
  line-height: 28px;
  text-align: left;
  padding: 0px;
  border-left: none;
  /* letter-spacing: -1px; */
}
/* individual play name styling */
.card-front .character-plays .name {
  font-family: "Crimson Text", serif;
  font-size: 21px;
  text-align: left;
  letter-spacing: -1px;
  word-spacing: -1px;
}
.card-front .character-plays .name > span:first-child {
  font-weight: 800;
  font-style: italic;
}
.card-front .character-plays .name > span:nth-child(2) {
  font-weight: 600;
  letter-spacing: -1px;
}
/* chatacter play text layout and styling */
.card-front .character-plays .text {
  grid-column-end: span 5;
  font-family: "Crimson Text", serif;
  font-size: 21px;
  line-height: 22px;
  text-align: left;
  white-space: pre-wrap;
  padding-bottom: 5px;
  letter-spacing: -1px;
}

.card-front .health {
  /* grid positioning */
  position: absolute;
  bottom: 22px;
  left: 24px;
  /* grid layout */
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  gap: 2px 5.3px;
}

.card-front .health-box {
  /* how a box/circle displays */
  width: 25px;
  height: 25px;
  border-radius: 25px;
  box-sizing: border-box;
  border: 1px solid black;
  background-color: white;
  /* positioning text and icons */
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 23px;
  /* text and icon styling */
  font-family: "Crimson Text", serif;
  font-weight: 600;
  color: var(--mom-color, var(--team-color));
  fill: var(--mom-color, var(--team-color));
}

/* quirks, card specific fixes, etc. */

/* smoke has too much text */
.Smoke .character-plays .Infuse.text {
  letter-spacing: -1.1px;
  word-spacing: -1.1px;
}

/* odd dual guild cards */

.Compound .playbook-result.momentus {
  background-image: linear-gradient(
    to right,
    var(--guild1-color) calc((1 - var(--col)) * 100%),
    var(--guild2-color) calc((5 - var(--col)) * 100%)
  );
}
.Lucky .playbook-result.momentus {
  background-image: linear-gradient(
    to right,
    var(--guild2-color) calc((0 - var(--col)) * 100%),
    var(--guild1-color) calc((4 - var(--col)) * 100%)
  );
}
