.App,.Main {
  text-align: center;
}

header {
  display: flex;
  flex-direction: row;
  background-color: #282c34;
  color: white;
}

.guild {
  display: inline-flex;
  justify-content: space-between;
  background-image: linear-gradient(
    to right,
    transparent 1em,
    black 1em,
    var(--guild-color) 4em,
    var(--guild-color) 5em,
    black calc(100% - 1em),
    transparent calc(100% - 1em)
  );
  text-shadow: 0.1em 0.1em 0.1em black;
}
.guild::after {
  border-left: 1em solid black;
  border-top: 1em solid transparent;
  border-bottom: 1em solid transparent;
  display: inline-block;
  content: "";
}
.guild svg {
  filter: drop-shadow(0px 0px 2px black);
}

.Cards {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  gap: 0.125in;
}

@media print {
  @page {
    margin: 0.25in;
  }
  header {
    display: none;
  }
  .Cards {
    display: block;
  }
  .nobreak {
    display: inline-block;
    break-inside: avoid;
    page-break-inside: avoid;
    margin: 0.0625in;
  }
}

